import React from 'react';
import PropTypes from 'prop-types';
import './ColorBox.css';

const ColorBox = ({ color }) => (
  <div className="color-box" style={{ backgroundColor: color }} />
);
ColorBox.propTypes = {
  color: PropTypes.string.isRequired,
};

export default ColorBox;



