import React, { Component } from 'react';
import Slider from 'react-rangeslider';
import tinycolor from 'tinycolor2';
import ColorBox from './ColorBox';
import InputControl from './InputControl';
import './App.css';

const orMin = (a, b) => (a < b ? b : a);
const orMax = (a, b) => (a > b ? b : a);
const orRange = (value, min, max) => orMax(orMin(value, min), max);

const functions = [
  {
    value: 'lighten',
    label: 'lighten',
    range: [
      0,
      100,
    ],
    function: (color, percent) => tinycolor(color)
      .lighten(parseInt(percent, 10))
      .toString(),
  },
  {
    value: 'darken',
    label: 'darken',
    range: [
      0,
      100,
    ],
    function: (color, percent) => tinycolor(color)
      .darken(parseInt(percent, 10))
      .toString(),
  },
  {
    value: 'saturate',
    label: 'saturate',
    range: [
      0,
      100,
    ],
    function: (color, percent) => tinycolor(color)
      .saturate(parseInt(percent, 10))
      .toString(),
  },
  {
    value: 'desaturate',
    label: 'desaturate',
    range: [
      0,
      100,
    ],
    function: (color, percent) => tinycolor(color)
      .desaturate(parseInt(percent, 10))
      .toString(),
  },
  {
    value: 'transparentize',
    label: 'transparentize',
    range: [
      0,
      100,
    ],
    function: (color, percent) => tinycolor(color)
      .setAlpha(parseInt(percent, 10) / 100)
      .toRgbString(),
  },
  {
    value: 'complement',
    label: 'complement',
    defaultOption: null,
    function: color => tinycolor(color)
      .spin(180)
      .toString(),
  },
  {
    value: 'invert',
    label: 'invert',
    defaultOption: null,
    function: (color) => {
      const { r, g, b } = tinycolor(color).toRgb();
      return tinycolor({
        r: 255 - r,
        g: 255 - g,
        b: 255 - b,
      }).toHexString();
    },
  },
  {
    value: 'grayscale',
    label: 'grayscale',
    defaultOption: null,
    function: color => tinycolor(color)
      .greyscale()
      .toString(),
  },
  {
    value: 'adjust-hue',
    label: 'adjust-hue',
    range: [
      -360,
      360,
    ],
    function: (color, deg) => tinycolor(color)
      .spin(parseInt(deg, 10))
      .toString(),
  },
];

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      functionIndex: 0,
      option: '0',
      inputColor: '#500000',
    };
  }

  updateInputColor = event => this.setState({ inputColor: event.target.value })

  updateFunction = event => this.setState({ functionIndex: parseInt(event.target.value, 10) })

  updateOption = option => this.setState({ option })

  updateOptionInput = event => this.updateOption(
    orRange(
      parseInt(event.target.value, 10),
      functions[this.state.functionIndex].range[0],
      functions[this.state.functionIndex].range[1],
    ),
  )

  render() {
    const { inputColor, functionIndex, option } = this.state;
    const isValid = tinycolor(inputColor).isValid();
    const outputColor = isValid ? functions[functionIndex].function(inputColor, option) : '';

    const hasOption = functions[functionIndex].range !== undefined;
    return (
      <div className="App wrapper">
        <style dangerouslySetInnerHTML={{
          __html: `
            .rangeslider-horizontal .rangeslider__fill {
              background-color: ${isValid ? tinycolor(inputColor).complement().toHexString() : '#e0e0e0'};
            }
            .textfield input:focus,
            .textfield textarea:focus,
            .select select:focus {
              border-color: ${isValid ? tinycolor(inputColor).complement().toHexString() : '#2e68cc'};
            }
          `,
        }}
        />
        <h1 className="App__header">SASS Colors <small>Live!</small></h1>
        <p>This app lets you test <a href="http://www.tutorialsteacher.com/sass/sass-color-functions">SASS color functions</a> live in the browser.</p>
        <p>Color calculations are done with <a href="https://bgrins.github.io/TinyColor/">TinyColor</a>.</p>
        <div className="grid" style={{ alignItems: 'center' }}>
          <div className="column--heavy">
            <InputControl
              label="Input Color:"
              value={inputColor}
              onChange={this.updateInputColor}
              className={isValid ? 'textfield' : 'textfield textfield--error'}
            />
          </div>
          <div>
            <ColorBox color={inputColor} />
          </div>
        </div>
        <div className="grid">
          <div>
            <label className="select">
              <select
                onChange={this.updateFunction}
                value={functionIndex}
                disabled={!isValid}
              >
                {
                  functions.map((func, i) => (
                    <option key={`${func.value}`} value={i}>
                      {func.label}
                    </option>
                  ))
                }

              </select>
              <span className="select__label" htmlFor="select">
                Which Function?
              </span>
            </label>
          </div>
        </div>
        {isValid && hasOption ? (
          <div className="grid">
            <div style={{ flexGrow: 24 }}>
              <Slider
                value={parseInt(option, 10)}
                onChange={this.updateOption}
                min={functions[functionIndex].range[0]}
                max={functions[functionIndex].range[1]}
              />
            </div>
            <div style={{ textAlign: 'center' }}>
              { isValid && hasOption ? (
                <InputControl value={option} label="Value:" onChange={this.updateOptionInput} />
              ) : null }
            </div>
          </div>
        ) : null }
        {isValid ? (
          <div className="grid">
            <div>
              <ColorBox color={outputColor} />
            </div>
            <div className="column--heavy">
              <InputControl label="Output Color:" value={outputColor} />
              <InputControl label="Output Function:" value={`${functions[functionIndex].value}(${inputColor}${hasOption ? `, ${option}` : ''})`} />
            </div>
          </div>
        ) : null }
      </div>
    );
  }
}

export default App;



